<template>
  <div class="add-commodity" v-loading="loading">
    <!--<y-tab class="tab" :tabList="tabList" @change="changeTab($event)"></y-tab>-->
    <div class="title-line">{{commodityId ? '' : ''}}</div>
    <div class="container">
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <el-select v-model="firstClassify" placeholder="" @change="selectFirstClassify($event)">
          <el-option
            v-for="item in firstClassifyList"
            :key="item.id"
            :label="item.classificName"
            :value="item.id">
          </el-option>
        </el-select>
        <el-select v-model="commodityData.classificId" placeholder="" @change="selectSecondClassify($event)">
          <el-option
            v-for="item in secondClassifyList"
            :key="item.id"
            :label="item.classificName"
            :value="item.id">
          </el-option>
        </el-select>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <el-radio-group v-model="commodityData.petSex">
          <el-radio :label="'01'"></el-radio>
          <el-radio :label="'02'"></el-radio>
        </el-radio-group>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <el-date-picker
          v-model="commodityData.petBirthday"
          type="date"
          value-format="yyyy-MM-dd"
          placeholder="">
        </el-date-picker>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <el-select v-model="commodityData.petGrade" placeholder="">
          <el-option
            v-for="item in petGradeList"
            :key="item.dictValue"
            :label="item.dictLabel"
            :value="item.dictValue">
          </el-option>
        </el-select>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <div class="input-box" v-for="(item,index) in commodityData.delivery" :key="index">
          <span>{{getDeliveryLabel(item.deliveryType)}}</span>
          <el-input v-model="item.deliveryPrice" placeholder=""></el-input>
        </div>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <el-checkbox border
                     v-for="(item,index) in guaranteeList" :key="index"
                     v-model="item.isChecked"
                     :label="item.dictLabel"
                     :disabled="false"
                     @change="changeGuarantee">
        </el-checkbox>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
        <div class="input-box">
          <el-input v-model="commodityData.prodPrice" placeholder=""></el-input>
        </div>
        <div class="input-box">
          <span></span>
          <el-input v-model="commodityData.discountPrice" placeholder=""></el-input>
        </div>
        <span class="tips"></span>
      </div>
      <!---->
      <div class="item-line auto-height">
        <span class="item-title"></span>
        <div class="list">
          <div class="line" v-for="(item,index) in commodityData.expParasite" :key="index">
            <el-select v-model="item.ventionOrder" placeholder="">
              <el-option
                v-for="item in vaccinesOrderList"
                :key="item.dictValue"
                :label="item.dictLabel"
                :value="item.dictValue">
              </el-option>
            </el-select>

            <el-date-picker
              v-model="item.ventionDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="">
            </el-date-picker>

            <el-input v-model="item.ventionModel" placeholder=""></el-input>

            <span>
            <i class="iconfont icon-plus-circle" @click="addExpParasite(commodityData.expParasite)"></i>
            <i v-if="index > 0" class="iconfont icon-minus-circle" @click="reduceExpParasite(index,commodityData.expParasite)"></i>
          </span>
          </div>
        </div>
      </div>
      <!---->
      <div class="item-line auto-height">
        <span class="item-title"></span>
        <div class="list">
          <div class="line" v-for="(item,index) in commodityData.vaccines" :key="index">
            <el-select v-model="item.ventionOrder" placeholder="">
              <el-option
                v-for="item in expParasiteOrderList"
                :key="item.dictValue"
                :label="item.dictLabel"
                :value="item.dictValue">
              </el-option>
            </el-select>

            <el-date-picker
              v-model="item.ventionDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="">
            </el-date-picker>

            <el-input v-model="item.ventionModel" placeholder=""></el-input>

            <span>
              <i class="iconfont icon-plus-circle" @click="addExpParasite(commodityData.vaccines)"></i>
              <i v-if="index > 0" class="iconfont icon-minus-circle" @click="reduceExpParasite(index,commodityData.vaccines)"></i>
            </span>
          </div>
        </div>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
      </div>
      <div class="editor-container">
        <el-input
          type="textarea"
          autosize
          placeholder=""
          v-model="commodityData.prodDesc">
        </el-input>
        <!--<ueditor ref="ueditor" :config="editorConfig" @on-change="ueditorChange($event)"></ueditor>-->
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title"></span>
      </div>
      <div class="editor-container">
        <pet-upload :imgUrl="commodityData.thumbnailUrl" @on-success="uploadSmallImage"></pet-upload>
      </div>
      <!---->
      <div class="item-line">
        <span class="item-title" style="overflow: visible">/</span>
      </div>
      <div class="editor-container">
        <!--<pet-upload v-for="(item,index) in swiperImgList" :key="index" @on-success="uploadSwiperImage($event,item)"></pet-upload>-->
        <pet-upload :imgUrl="commodityData.topDrawUrl" @on-success="uploadSwiperImage($event,item)"></pet-upload>
      </div>
      <!---->
      <div class="item-line" v-if="commodityId">
        <span class="item-title"></span>
        <el-button class="btn down-btn" :class="{'selected': isSelectedDownBtn}" @click="downCommodity" :loading="switchStatus"></el-button>
        <!--<button class="btn down-btn" :class="{'selected': isSelectedDownBtn}" @click="downCommodity" :loading="switchStatus"></button>-->
        <span class="tips"></span>
      </div>
      <!---->
      <div class="footer">
        <el-button type="danger" @click="preview"></el-button>
        <el-button type="danger" @click="audit"></el-button>
        <el-button type="info" @click="quit"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
/*eslint-disable*/
import _cloneDeep from 'loadsh/cloneDeep'

const token = localStorage.getItem('P_S_TOKEN_KEY');

export default {
  props:{
    config: {
      type: Object,
      default: function (){
        return {}
      }
    }
  },
  components: {},
  data(){
    let _this = this;
    return {
      loading: false,
      tabList: ['',''],
      commodityData: {
        "classificId": '',  //id
        "delivery": [
          // {
          //   deliveryType: '',
          //   deliveryPrice: ''
          // }
        ],  //
        "discountPrice": '',  //
        "expParasite": [
          {
            ventionDate: '',
            ventionModel: '',
            ventionOrder: '',
          }
        ],  //
        "guarantee": "", //
        "id": '',
        "imageUrl": "",
        "petBirthday": "",  //
        "petGrade": "", //
        "petSex": "", //
        "prodDesc": "", //
        "prodPrice": '',  //
        "thumbnailUrl": "", //
        "topDrawUrl": "", ///
        "vaccines": [
          {
            ventionDate: '',
            ventionModel: '',
            ventionOrder: '',
          }
        ],  //
      },
      firstClassify: '',
      firstClassifyList: [],
      secondClassify: '',
      secondClassifyList: [],
      petGradeList: [],
      petBirthday: '',
      imageUrl: '',
      isSelectedDownBtn: false,
      editorConfig: {
          toolbars: [
            ['fullscreen', 'source','|', 'undo', 'redo','|','bold', 'italic', 'underline', 'fontborder', 'strikethrough',
              '|','superscript','subscript','|', 'forecolor', 'backcolor','|', 'removeformat','|', 'insertorderedlist', 'insertunorderedlist',
              '|','selectall', 'cleardoc','fontfamily','fontsize','justifyleft','justifyright','justifycenter','justifyjustify','|',
              'link','unlink']
          ],
        autoHeightEnabled: false,
        initialContent:'',   //,textarea/script
        initialFrameWidth: null,
        initialFrameHeight: 450,
      },
      headerToken: token,
      uploadingImage: false,
      swiperImgList: [
        {
          imgUrl: ''
        }
      ],
      deliveryTypeList: [],
      guaranteeList: [],
      vaccinesOrderList: [],
      expParasiteOrderList: [],
      commodityId: '',
      switchStatus: false,
    }
  },
  methods:{
    //tab
    changeTab(index){
      console.log(index)
    },
    //
    selectFirstClassify(id){
      this.getSecondClassifyList(id)
    },
    //
    selectSecondClassify(id){
      console.log(id)
    },
    //
    getFirstClassifyList(){
      this.$http.get('api/mgmt/public/classific/children?pId=0').then((res) => {
        if (res.code === 1000) {
          this.firstClassifyList = res.data;
        }else{
          this.$message({
            message: res.message,
            type: 'error'
          })
        }
      })
    },
    //
    getSecondClassifyList(id){
      this.$http.get('api/mgmt/public/classific/children?pId='+id).then((res) => {
        if (res.code === 1000) {
          this.secondClassifyList = res.data;
        }else{
          this.$message({
            message: res.message,
            type: 'error'
          })
        }
      })

    },
    //
    setCommodityDelivery(){
      this.commodityData.delivery = [];
      this.deliveryTypeList.forEach((item,index)=>{
        this.commodityData.delivery.push({
          deliveryPrice: '',
          deliveryType: item.dictValue
        })
      })
    },
    //
    getDeliveryLabel(type){
      let delivery = this.deliveryTypeList.find((item)=>{ return item.dictValue === type });
      return delivery&&delivery.dictLabel;
    },
    //
    setCommodityGuarantee(){
      this.guaranteeList.forEach((item,index)=>{
        this.$set(item, 'isChecked', this.commodityData.guarantee.indexOf(item.dictValue) > -1)
      })
    },
    //
    changeGuarantee(){
      let guaranteeList = [];
      this.guaranteeList.forEach((item,index)=>{
        item.isChecked ? guaranteeList.push(item.dictValue) : '';
      })
      this.commodityData.guarantee = guaranteeList.join(',');
    },
    //
    addExpParasite(list){
      list.push({
        ventionDate: '',
        ventionModel: '',
        ventionOrder: '',
      })
    },
    //
    reduceExpParasite(index,list){
      list.splice(index,1);
    },
    ueditorChange(data){
      console.log('ueditor content: ',data)
    },
    uploadSmallImage(data){
      this.commodityData.thumbnailUrl = data.imageSmallUrl
    },
    uploadSwiperImage(data,item){
      this.commodityData.topDrawUrl = data.imageSmallUrl;
      // item.imgUrl = data.imageSmallUrl;
      // this.swiperImgList.length < 5 ? this.swiperImgList.push({imgUrl:''}) : '';
    },
    //
    preview(){},
    //
    audit(){
      if(!this.checkValid()) return;
      let params = _cloneDeep(this.commodityData);

      this.commodityId ? this.updateCommodity(params) : this.addCommodity(params);
      this.loading = true;
    },
    //
    checkValid(){
      let result = true;
      let checkDelivery = (list)=>{
        let hasInput = false;
        list.forEach((item,index)=>{
          item.deliveryPrice !== '' ? hasInput = true : '';
        })
        return hasInput;
      };
      let checkPrevention = (list)=>{
        let hasInput = false;
        list.forEach((item,index)=>{
          item.ventionOrder&&item.ventionModel&&item.ventionDate ? hasInput = true : '';
        })
        return hasInput;
      };

      if(!this.commodityData.classificId){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.petSex){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.petBirthday){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.petGrade){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!checkDelivery(this.commodityData.delivery)){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.guarantee){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.prodPrice){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!checkPrevention(this.commodityData.vaccines)){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!checkPrevention(this.commodityData.expParasite)){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.prodDesc){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.thumbnailUrl){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      else if(!this.commodityData.topDrawUrl){
        this.$message({
          message: '',
          type: 'error'
        })
        result = false;
      }
      return result;
    },
    //
    addCommodity(params){
      this.$http.post('api/mgmt/mall/prod/save', params).then((res)=>{
        this.loading = false;
        if(res.code === 1000){
          this.$message({
            message: '',
            type: 'success'
          })

          this.quit();
        }
      })
    },
    //
    updateCommodity(params){
      this.$http.put('api/mgmt/mall/prod/save', params).then((res)=>{
        this.loading = false;
        if(res.code === 1000){
          this.$message({
            message: '',
            type: 'success'
          })

          this.quit();
        }
      })
    },
    //
    downCommodity(){
      let params = {
        ids: this.config.id,
        isUpper: !this.isSelectedDownBtn
      }
      //
      this.switchStatus = true;
      this.$http.post('api/mgmt/mall/prod/handle',params).then((res)=>{
        this.switchStatus = false;
        if(res.code === 1000){
          this.$message({
            message: this.isSelectedDownBtn ? '' : '' + '',
            type: 'success'
          })
          this.isSelectedDownBtn = !this.isSelectedDownBtn;
        }
      })
    },
    //
    getCommodityDetail(){
      this.loading = true;
      this.$http.get('api/mgmt/mall/prod/view/'+this.commodityId).then((res)=>{
        this.loading = false;
        if(res.code === 1000){
          this.commodityData = res.data;
          this.setCommodityGuarantee();
        }
      })
    },
    //
    quit(){
      this.$router.push({path: '/manage/commodity/online'})
    },
  },
  created(){
    this.getFirstClassifyList();
    //
    this.petGradeList = _cloneDeep(this.$store.state.gradeList)
    //
    this.deliveryTypeList = _cloneDeep(this.$store.state.deliveryTypeList);
    this.setCommodityDelivery();
    //
    this.guaranteeList = _cloneDeep(this.$store.state.guaranteeList);
    this.setCommodityGuarantee();
    //
    this.vaccinesOrderList = _cloneDeep(this.$store.state.vaccinesOrderList);
    this.expParasiteOrderList = _cloneDeep(this.$store.state.expParasiteOrderList);

    //urlid
    this.commodityId = this.$route.query&&this.$route.query.id;
    this.commodityId ? this.getCommodityDetail() : '';
  },
}
</script>
<style lang="less" scoped>
  .add-commodity{
    .tab{
      height: 44px;
      background-color: #F0F0F2
    }
    .title-line{
      width: 96%;
      height: 22px;
      margin: 0 2%;
      padding: 10px 0;
      border-bottom: 2px solid #E0E0E0;
    }
    .container{
      width: 98%;
      padding-left: 2%;
      padding-top: 20px;
      overflow: auto;
      height: ~'calc(100% - 64px)';
      .item-line{
        margin-bottom: 10px;
        height: 40px;
        white-space: nowrap;
        position: relative;
        .item-title{
          display: inline-block;
          width: 100px;
          height: 100%;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          vertical-align: top;
        }
        .input-box{
          display: inline-block;
          margin-right: 20px;
        }
        .list{
          display: inline-block;
          width: ~'calc(100% - 120px)';
          .line{
            margin-bottom: 10px;
          }
        }
        .tips{
          color: rgba(168, 168, 168, 1);
        }
        .down-btn{
          border-color: #A8A8A8;
          color: #A8A8A8;
          background-color: white;
        }
        .down-btn.selected{
          color: white;
          background-color: #409EFF;
          border-color: #409EFF;
        }
      }
      .auto-height{
        height: auto;
      }
      .editor-container{
        margin-bottom: 20px;
      }
      .footer{
        border-top: 2px solid #E0E0E0;
        padding-top: 40px;
        margin-bottom: 40px;
        text-align: center;
      }
    }
  }
</style>



